/* Error Styling */

.error label {
  color: #cc0033;
}

.error input[type=text], [type=date], [type=time]{
  background-color: #fce4e4;
  border: 1px solid #cc0033;
  outline: none;
}
.error textarea[type=text]{
  background-color: #fce4e4;
  border: 1px solid #cc0033;
  outline: none;
}
.error select[type=text]{
  background-color: #fce4e4;
  border: 1px solid #cc0033;
  outline: none;
}

.error .error-message {
  display: inline-block;
}

